$mlight: #e9e9e9 !default;
$maqua: #7CC7C6 !default;
$myellow: #E9C152 !default;
$mred: #E74856 !default;
$mviol: #231A25 !default;
$mlight2: #F7F7F7 !default;
$maqua2: #8edfdd !default;
$myellow2: #ffda75 !default;
$mred2: #fa6f7b !default;
$mviol2: #513f55 !default;

$theme-colors: (
    "mlight": $mlight,
    "maqua": $maqua,
    "myellow": $myellow,
    "mred": $mred,
    "mviol": $mviol,
    "mlight2": $mlight2,
    "maqua2": $maqua2,
    "myellow2": $myellow2,
    "mred2": $mred2,
    "mviol2": $mviol2,
) !default;
